import React from 'react'


const Test = (props) => {
  console.log('props.hello', props.hello)
  return <div>Test Componet {props.hello}</div>
}


function Items() {
  return (
    <>
      <div></div>
      <div />

      <div></div>

      <div>{false}</div>

      <div>{null}</div>

      <div>{undefined}</div>

      <div>{true}</div>

      {/* ----[ ]--- */}
      {true && ''}
      {true && true}
      {true && <Test hello=' ' />}
      {false && <Test />}
      {0 && <Test />}
      {<Test /> && false}


    </>
  )
}

export default Items